.example {
  display: grid;
  transition: all 0.5s;
  user-select: none;
  background: linear-gradient(to bottom, white, black);
}
#header {
  color: #4D926F;
  margin: 20px;
}
#header .nested {
  font-size: 15px;
}
@media (max-width: 600px) {
  #header {
    color: #3b7156;
  }
}
.widget {
  color: #efefef;
  background-color: #efefef;
}
.block {
  color: red;
  color: blue;
}
.block .inner {
  background-color: blue;
}
